.clock {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  width: 200px;
  background: url("./img/clock.png") white;
  background-size: cover;
  border-radius: 50%;
  box-shadow: inset 2px 2px 12px 3px #cfc8c8;
  border: 15px solid white;
  margin-left: 20%;
}

.clock::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: #42a5f5;
  border-radius: 50%;
  z-index: 1000;
}

.hour,
.minute,
.second {
  position: absolute;
}

.hour,
.hr {
  height: 100px;
  width: 100px;
  justify-content: center;
  display: flex;
}
.hr::before {
  content: "";
  position: absolute;
  width: 6px;
  height: 50px;
  background: black;
  border-radius: 6px 6px 0 0;
}

.minute,
.min {
  height: 120px;
  width: 120px;
  justify-content: center;
  display: flex;
}
.min::before {
  content: "";
  position: absolute;
  width: 5px;
  height: 65px;
  background: rgb(0, 0, 0);
  border-radius: 6px 6px 0 0;
}

.second,
.sec {
  height: 150px;
  width: 150px;
  justify-content: center;
  display: flex;
}
.sec::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 90px;
  background: red;
  border-radius: 6px 6px 0 0;
  margin: 4px;
}
